nav{
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.5rem 1.5rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translate(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.6rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

}

nav a{
    background: transparent;
    width: 4rem;
    padding: 1.5rem;
    border-radius: 75%;
    display: flex;
    color: var(--color-light);
}

nav a :hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-primary);
    color: var(--color-bg);
}

